import Image from "next/image";

const SectionOne = () => {
  return (
    <div className="relative w-full h-screen overflow-hidden">
      <div className="relative w-full h-full flex flex-col sm:flex-row z-0">
        {/* Lado esquerdo: imagem de fundo (esconde no mobile) */}
        <div className="w-full sm:w-1/2 h-full relative">
          <Image
            src="/assets/bg-image2-section1.png"
            alt="Fundo esquerdo"
            fill
            className="object-cover"
          />
        </div>

        
        <div className="w-full sm:w-1/2 h-full relative">
          <div className="absolute inset-0 hidden sm:block">
            <Image
              src="/assets/bg-image-section1.png"
              alt="Fundo direito"
              fill
              className="object-cover bg-gradient-to-l"
            />
          </div>

          <div className="absolute inset-0 flex flex-col items-center justify-center text-white sm:px-16 gap-12 md:px-28 z-10">
            <div className="items-center flex justify-center flex-col" >
              <div className="relative w-20 h-20 sm:w-32 sm:h-16">
                <Image
                  src="/assets/icon-image-pretorian.png"
                  alt="Ícone Pretorian"
                  fill
                  className="object-contain"
                />
              </div>
              <p style={{ fontFamily: 'AtypDisplaySemiBold' }} className="text-3xl sm:text-5xl ">
                pretorian
              </p>
              <p style={{fontFamily: 'AtypDisplayLight'}} className="text-base sm:text-4xl text-[#d4b866] tracking-widest">
                asset
              </p>
            </div>

            <span style={{fontFamily: 'AtypDisplay'}} className="sm:text-lg md:text-xl lg:text-2xl max-w-4xl text-white">
              Unimos a força de uma empresa que já impactou mais de <strong>R$ 2 bilhões</strong> em operações no Brasil, com a inteligência operacional
              de quem vive e entende o que é <strong>escalar negócios</strong>.
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SectionOne;
